import { Box, List, ListItem, TextField } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "redux";

export const TestPageTemplateB_1: React.FC<ITPT_B_1> = ({
  question,
  questionTextGroup1,
  questionTextGroup2,
}) => {
  const [curAns, setCurAnswer] = useState("");
  const curStateAnswer = useSelector((state: SiteState) => state.curAnswer);
  const dispatch: Dispatch<any> = useDispatch();
  useEffect(() => {
    if (curStateAnswer === "") {
      setCurAnswer("");
    }
  }, [curStateAnswer]);
  useEffect(() => {
    dispatch({
      type: "SetAnswer",
      curAns: curAns.toUpperCase(),
    });
  }, [curAns]);
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setCurAnswer(e.target.value);
  };
  return (
    <div className="TestPageTemplateB_0">
      <Box fontSize={30} className="TPT_A_2_q">
        {question}
      </Box>
      <List>
        <ListItem>
          <ul>
            <li>{questionTextGroup1[0]}</li>
            <li>{questionTextGroup1[1]}</li>
            <li>{questionTextGroup1[2]}</li>
            <li>{questionTextGroup1[3]}</li>
            <li>{questionTextGroup1[4]}</li>
          </ul>
        </ListItem>
      </List>
      <List>
        <ListItem>
          <ul>
            <li>{questionTextGroup2[0]}</li>
            <li>{questionTextGroup2[1]}</li>
            <li>{questionTextGroup2[2]}</li>
            <li>{questionTextGroup2[3]}</li>
            <li>{questionTextGroup2[4]}</li>
          </ul>
        </ListItem>
      </List>
      <TextField
        id="outlined-basic"
        label=""
        variant="outlined"
        value={curAns}
        onChange={handleChange}
      />
    </div>
  );
};
