import {
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "redux";
import { TestBase } from "../TestPage/Tests";

const useStyles = makeStyles({
  table: {
    minWidth: 900,
  },
});
export const Tasks: React.FC = () => {
  const classes = useStyles();
  const dispatch: Dispatch<any> = useDispatch();
  const curUserFio = useSelector((state: SiteState) => state.curUserFio);
  const [baseTasks, setBaseTasks] = useState<any[]>([]);
  const [rows, setRows] = useState<any[]>([]);
  const [ taskIds, settasksIds ] = useState<any[]>([]);
  function createData(TaskName: string, TaskDate: string) {
    return { TaskName, TaskDate };
  }
  useEffect(() => {
    fetch(`https://cab07.000webhostapp.com/sql_getTasks.php`)
      .then((res) => res.json())
      .then((result) => {
        let arr: any[] = [];
        let tarr: any[] = [];
        result.tasks.map((el: any) => {
          console.log(el.task_user_fio);
          if (el.task_user_fio === curUserFio ) {
            arr.push({
              TaskId : el.task_id,
              TaskName: el.task_test_name,
              TaskStartTime: el.task_start_time,
              TaskEndTime: el.task_end_time,
            });
            tarr.push( el.task_id );
          }
        });
        setBaseTasks(arr);
        settasksIds(tarr);
      });
  }, []);
  useEffect(() => {
    setRows([]);
    let arr: any[] = [];
    baseTasks.map((el) => {
      arr.push(createData(el.TaskName, el.TaskEndTime));
    });

    setRows(arr);
  }, [baseTasks]);

  const showTest = (f: string, a:number) => {
    alert( taskIds[a] )
    dispatch({
      type: "ShowTest",
      curTestSet: f,
      curTaskId : taskIds[a]
    });
  };
  return (
    <div className="Tasks">
      <TableContainer>
        <Table
          className={classes.table}
          size="small"
          aria-label="a dense table"
        >
          <TableHead>
            <TableRow>
              <TableCell align="center">Task name</TableCell>
              <TableCell align="center">Task Date</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, ind) => (
              <TableRow
                key={row.TaskName}
                onClick={() => showTest(row.TaskName, ind)}
                hover
              >
                <TableCell component="th" scope="row" align="center">
                  {row.TaskName}
                </TableCell>
                <TableCell align="center">{row.TaskDate}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};
