import {
  makeStyles,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "redux";

const useStyles = makeStyles({
  root: {
    width: "100%",
    height: "100%",
  },
  container: {
    minWidth: 900,
    maxHeight: 700
  },
});

export const AdminResults: React.FC = () => {
  const classes = useStyles();
  const dispatch: Dispatch<any> = useDispatch();

  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [curUserTestsResults, setCurUserTestsResult] = useState<any[]>([]);
  const [rows, setRows] = useState<any[]>([]);

  useEffect(() => {
    fetch(`https://cab07.000webhostapp.com/sql_getTestsResults.php`)
      .then((res) => res.json())
      .then((result) => {
        let arr: any[] = [];
        result.results.map((el: any) => {
          arr.push({
            TestUser: el.user_fio,
            TestName: el.test_name,
            TestDate: el.dateTime,
            TestResult: el.result,
            TestId: el.test_id
          });
        });
        setCurUserTestsResult(arr.reverse());
      });
  }, []);

  const handleChangePage = (event: any, newPage: any) => {
    setPage(newPage);
  };  
  const handleChangeRowsPerPage = (event: any) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };
  
  useEffect(() => {
    setRows([]);
    let arr: any[] = [];
    curUserTestsResults.map((el) => {
      arr.push(
        createData(el.TestId, el.TestUser, el.TestName, el.TestDate, el.TestResult)
      );
    });
    setRows(arr);
  }, [curUserTestsResults]);
  const columns = [
    { id: "TestUser", label: "TestUser", minWidth: 170, align: "center" },
    { id: "TaskName", label: "TaskName", minWidth: 170, align: "center" },
    { id: "TaskDate", label: "TaskDate", minWidth: 100, align: "center" },
    { id: "TaskResult", label: "TaskResult", minWidth: 170, align: "center" },
  ];

  function createData(
    TestId: number,
    TestUser: string,
    TaskName: string,
    TaskDate: string,
    TaskResult: string
  ) {
    return { TestId, TestUser, TaskName, TaskDate, TaskResult };
  }
  const trClickHandler = ( testResultID : number ) => {
    dispatch(
      {
        type : "ShowAdminTestInfo",
        curTestResultID: testResultID/1
      }
    )
  };

  return (
    <div className="TestsResults">
      <Paper className={classes.root}>
        <TableContainer className={classes.container}>
          <Table stickyHeader aria-label="sticky table">
            <TableHead>
              <TableRow>
                {columns.map((column) => (
                  <TableCell
                    key={column.id}
                    align="center"
                    style={{ minWidth: column.minWidth }}
                  >
                    {column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row) => {
                  return (
                    <TableRow
                      hover
                      role="checkbox"
                      tabIndex={-1}
                      key={row.TestId}
                      onClick = { () => trClickHandler( row.TestId )}
                    >
                      <TableCell key={row.TaskUser} align="center">
                        {row.TestUser}
                      </TableCell>
                      <TableCell key={row.TaskName} align="center">
                        {row.TaskName}
                      </TableCell>
                      <TableCell key={row.TaskDate} align="center">
                        {row.TaskDate}
                      </TableCell>
                      <TableCell key={row.TaskResult} align="center">
                        {row.TaskResult}
                      </TableCell>
                    </TableRow>
                  );
                })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[8, 16, 32]}
          component="div"
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      </Paper>
    </div>
  );
};
