import {
  MenuItem,
  Select,
  makeStyles,
  FormControl,
  InputLabel,
  Button,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

export const AdminAddTask: React.FC = () => {
  const classes = useStyles();
  const [user, setUser] = useState("");
  const [test, setTest] = useState("");
  const [selectedDate, setSelectedDate] = useState("");

  const [ users, setUsers] = useState<any[]>([]);
  const [ tests, setTests] = useState<any[]>([]);

  const handleUserChange = (event: any) => {
    setUser(event.target.value as string);
  };
  const handleTestChange = (event: any) => {
    setTest(event.target.value as string);
  };
  const handleDateChange = (event:any) => {
    setSelectedDate(event.target.value as string);
  };
  const clickButHandler = () =>{
    alert( `Set ${test} to ${user} ended ${selectedDate}`);
    fetch(`https://cab07.000webhostapp.com/sql_addTasks.php?
                USER_FIO="`+user+
                `"&TEST_NAME="`+test+
                `"&START_DATE_TIME=`+selectedDate+
                `&END_DATE_TIME=`+selectedDate)
        .then(res => res.json())
        .then(
          (result) => {           
                console.log ( result )           
        });
  }
  useEffect(()=> {
    let arr:any[] = [];
    let att:any[] = [];    
    fetch(`https://cab07.000webhostapp.com/sql_Users.php`)
    .then(res => res.json())
    .then(      
      (results) => {        
        results.users.map ( (result:any) =>{
          arr.push(
            <MenuItem value= {result.user_fio}>{result.user_fio}</MenuItem> ); 
        })                           
      }      
    );
    setUsers(arr);
    
    fetch(`https://cab07.000webhostapp.com/sql_Tests.php`)
    .then(res => res.json())
    .then(
      (results) => {    
          results.tests.map ( (result:any) => {
            att.push(
              <MenuItem value = {result.test_name}>{result.test_name}</MenuItem>)
          })                          
    });
    
    setTests(att);       
},[]);
  return (
    <div className="AdminAddTask">
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-label">User</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={user}
          onChange={handleUserChange}
        >
          { users }
        </Select>
      </FormControl>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-label">Test</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={test}
          onChange={handleTestChange}        >
         {tests}
        </Select>
      </FormControl>
      <input 
        type = "date"
        onChange = {handleDateChange}></input>
      <Button
        variant="contained"
        color="primary"               
        onClick={clickButHandler}
      >
        Set Test
      </Button>
     </div>
  );
};
