import {
  makeStyles,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "redux";

const useStyles = makeStyles({
  root: {
    width: "100%",
    height: "100%",
  },
  container: {
    minWidth: 900,
    maxHeight: 700,
  },
});

export const TestsResults: React.FC = () => {
  const classes = useStyles();
  const dispatch: Dispatch<any> = useDispatch();
  const curUserFio = useSelector( (state:SiteState) => state.curUserFio);

  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [ curUserTestsResults, setCurUserTestsResult ] = useState<any[]>([]);
  const [ rows, setRows ] = useState<any[]>([]);
  
  useEffect (() => {
    fetch(`https://cab07.000webhostapp.com/sql_getTestsResults.php`)
    .then(res => res.json())
    .then(
      (result) => { 
        let arr:any[] = [];         
        result.results.map ( (el:any) => {
          if ( el.user_fio === curUserFio ) {
            arr.push(
              {
                TestName : el.test_name,
                TestDate : el.dateTime,
                TestResult : el.result
              }              
            )
          }
        })              
        setCurUserTestsResult( arr.reverse() );                     
    });  
  }, [])
  
  const handleChangePage = (event: any, newPage: any) => {
    setPage(newPage);
  };
 
  useEffect(()=>{    
    setRows([]);
    let arr:any[] = [];
    curUserTestsResults.map ( (el) => {
       arr.push(
         createData( el.TestName, el.TestDate, el.TestResult)
       )
    })
    setRows(arr);
  },[curUserTestsResults])
  const handleChangeRowsPerPage = (event: any) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };
  const columns = [
    { id: "TaskName", label: "TaskName",  minWidth: 170, align: "center" },
    { id: "TaskDate", label: "TaskDate",  minWidth: 100, align: "center" },
    { id: "TaskResult", label: "TaskResult", minWidth: 170, align: "center"},     
  ];

  function createData(    
    TaskName: string,
    TaskDate : string,
    TaskResult: string 
  ) {   
    return { TaskName, TaskDate, TaskResult };
  }

  
  return (
    <div className="TestsResults">
      <Paper className={classes.root}>
        <TableContainer className={classes.container}>
          <Table stickyHeader aria-label="sticky table">
            <TableHead>
              <TableRow>
                {columns.map((column) => (
                  <TableCell
                    key={column.id}
                    align="center"
                    style={{ minWidth: column.minWidth }}
                  >
                    {column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row) => {
                  return (
                    <TableRow
                      hover
                      role="checkbox"
                      tabIndex={-1}
                      key={row.TaskName}>                
                          <TableCell key={row.TaskName} align="center">{row.TaskName}</TableCell>
                          <TableCell key={row.TaskDate} align="center">{row.TaskDate}</TableCell>
                          <TableCell key={row.TaskResult} align="center">{row.TaskResult}</TableCell>                                             
                    </TableRow>
                  );
                })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[10, 25, 100]}
          component="div"
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      </Paper>
    </div>
  );
};
