  const x:ITestResult = {
    TestUser: "",
    TestName: "",
    TestResult: 1,    
  }
export const AdminTestInfo = () => {
  const testResId = useSelector((state:SiteState) => state.curTestResultID)
  const [curTestResults, setCurUserTestsResult] = useState<ITestResult>(x);
  const [curTest, setCurTest] = useState<any[]>([]);
  const [ userDate, setUserData ] = useState<any[]>([]);

    useEffect(() => {
      let arr: ITestResult;
      let usArr: any[] = [];
      fetch(
        `https://cab07.000webhostapp.com/sql_getTestsResultByID.php?testResultID=${testResId}`
      )
        .then((res) => res.json())
        .then((result) => {
          result.results.map((el: any) => {
            arr = {
              TestUser: el.user_fio,
              TestName: el.test_name,
              TestResult: el.result,                           
            };
            let s:string[] = el.answers.split("@");
            s.map ( (qt:any ) => {
              let wt:any[] = qt.split("_");
              usArr.push({
                id: wt[0],
                right: wt[2]==="-"?false:true,
                theme: wt[1],
                ans: wt[3]
              })   
            })
           
          });
          setCurUserTestsResult(arr);
          setUserData(usArr);
        });
    }, [testResId]);
  
  useEffect(() => {
    TestBase.map((el) => {
      console.log(`${el.testSetName} _ ${curTestResults.TestName}`)
      if (el.testSetName === curTestResults.TestName) {
              setCurTest(el.questions);
      }
    });  
  }, [curTestResults]);

  return (
    <div className="AdminTestInfo">
      <div>
      <p className = "ATI_p">{curTestResults.TestUser}</p>
      <p className = "ATI_p">{curTestResults.TestName}</p>
      <p className = "ATI_p">{`${(curTestResults!.TestResult/1)} / ${curTest!.length}  (${ (curTestResults!.TestResult / curTest!.length) * 100}%)`}</p>

       
      </div>
      <div>
        <ATI_Answers curTest={curTest} userData={userDate} />
      </div>
      <div>
        <div>
          <ATI_AllQuests curTest={curTest} />
        </div>
      </div>
    </div>
  );
};

