import { Button, TextField } from "@material-ui/core";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { Dispatch } from "redux";

export const LoginPage: React.FC = () => {
  const dispatch: Dispatch<any> = useDispatch();
  const [login, setLogin] = useState("");
  const [pass, setPass] = useState("");

  let accToBase = () => {
    if ( login !== "" && pass !=="") {
      fetch(
        `https://cab07.000webhostapp.com/sql_login.php?login=` +
          login +
          `&pass=` +
          pass +
          ``
      )
        .then((res) => res.json())
        .then(
          (result) => {
            if (result.userId !== -1) {
              dispatch({
                type: "LogIn",
                curUserId: result.userId,
                curUserFio: result.userFIO,              
                curUserSchool: result.userSchool,
                curUSerClass: result.userClass,
              });
            } 
            if (result.userId === -1) {
              dispatch({
                type: "LogIn",
                curUserId: 0,
                curUserFio: "admin"         
              });
            }
          },
          // Примечание: важно обрабатывать ошибки именно здесь, а не в блоке catch(),
          // чтобы не перехватывать исключения из ошибок в самих компонентах.
          (error) => {
            alert("Login pass error " + login + "___" + pass);
          }
        );
    }
    else {
      alert( "Not empty! ");
    }
    
  };
  const LoginButHandler = () => {
   accToBase();
  };
  const loginHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
    setLogin(e.target.value)
  }
  const passHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPass(e.target.value)
  }
  return (
    <div className="LoginPage">
      <TextField id="standard-basic" className="LP_login" label="Login" onChange = {loginHandler} value = {login}/>
      <TextField id="standard-basic" className="LP_pass" type="password" label="Pass" onChange = {passHandler} value = {pass}/>
      <Button variant="contained" color="primary" onClick={LoginButHandler}>
        Enter
      </Button>
    </div>
  );
};
