import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { TestBase } from "./Tests";
import { Dispatch } from "redux";

import { Button } from "@material-ui/core";
import { TestPageTemplateA_0 } from "./TestPageTemplates/TestPageTemplateA_0";
import { TestPageTemplateA_1 } from "./TestPageTemplates/TestPageTemplateA_1";
import { TestPageTemplateA_2 } from "./TestPageTemplates/TestPageTemplateA_2";
import { TestPageTemplateB_0 } from "./TestPageTemplates/TestPageTemplateB_0";
import { TestPageTemplateB_1 } from "./TestPageTemplates/TestPageTemplateB_1";

export const TestPage: React.FC = () => {
  const curTestSet = useSelector((state: SiteState) => state.curTestSet);
  const curAnswer = useSelector((state: SiteState) => state.curAnswer);
  const curUserFIO = useSelector((state: SiteState) => state.curUserFio);
  const curTaskId = useSelector((state:SiteState) => state.curTaskId );

  const dispatch: Dispatch<any> = useDispatch();
  const [curTests, setCurTests] = useState([]);
  const [curIndex, incrCurIndex] = useState(0);
  const [rightArr, setRightArr] = useState([]);
  const [curRightCount, incrRightCount] = useState(0);
  const [ curThemes, setCurThemes ] = useState([]);
  const [ curIndexes, setCurIndexes] = useState([]);
  

  const [curTestResults, setCurTestsResults ] = useState<string[]>([]);

  useEffect(() => {
    alert( curTaskId )
    let arr: any = [];
    let rarr: any = [];
    let themes: any = [];
    let indexes: any = [];
    TestBase.map((el) => {
      if (el.testSetName === curTestSet) {
        el.questions.map((al:IQuestion) => {
          switch (al.type) {
            case "A_0":
              arr.push(
                <TestPageTemplateA_0
                  question={al.quest}
                  answers={al.answers || []}
                  right={al.right}
                />
              );
              break;
            case "A_1":
              arr.push(
                <TestPageTemplateA_1
                  question={al.quest}
                  answers={al.answers || []}
                  right={al.right}
                />
              );
              break;
            case "A_2":
              arr.push(
                <TestPageTemplateA_2
                  question={al.quest}
                  answers={al.answers || []}
                  questText={al.questText}
                  right={al.right}
                />
              );
              break;
            case "B_0":
              arr.push(
                <TestPageTemplateB_0
                  question={al.quest}
                  questionText={al.questText}
                />
              );
              break;
            case "B_1":
              let tt:string[]= [];
              if (al.questTextArr) { tt = al.questTextArr}
             
              arr.push(
                <TestPageTemplateB_1
                  question={al.quest}
                  
                  questionTextGroup1={[
                    tt[0],
                    tt[1],
                    tt[2],
                    tt[3],
                    tt[4]
                  ]}
                  questionTextGroup2={[
                    tt[5],
                    tt[6],
                    tt[7],
                    tt[8],
                    tt[9]
                  ]}
                />
              );
          }
          rarr.push(al.right);
          themes.push(al.theme);
          indexes.push(al.id);
        });
      }
    });
    setCurTests(arr);
    setRightArr(rarr);
    setCurThemes(themes);
    setCurIndexes(indexes);
  }, []);
  const clickHandler = () => {
    let arr:string[] = curTestResults;
    let q = curThemes[curIndex];
    let w = (curAnswer === rightArr[curIndex]) ? "+":"-";
    let e = curAnswer as string;
    let r = curIndexes[curIndex];
      arr.push( `${r}_${q}_${w}_${e}`);
    setCurTestsResults(arr);
    
    if (curAnswer === rightArr[curIndex]) {
      incrRightCount(curRightCount + 1);
    }
    if (curIndex < curTests.length - 1) {
      incrCurIndex(curIndex + 1);
    } else {
      alert("RightCount = " + curRightCount);     
      let dt = Date.now();
      fetch(`https://cab07.000webhostapp.com/sql_setTestResult.php?
                user_fio="`+curUserFIO+`"&
                test_name="`+curTestSet+`"&
                date_time="`+ (""+new Date(dt)).split("G")[0]+`"&
                user_result=`+curRightCount+`&
                user_answers="`+curTestResults.join("@")+`"`)
            .then(res => res.json())
            .then(
            (result) => {   
                      
            },
            // Примечание: важно обрабатывать ошибки именно здесь, а не в блоке catch(),
            // чтобы не перехватывать исключения из ошибок в самих компонентах.
            (error) => {})          
            alert(`QQQQhttps://cab07.000webhostapp.com/setTaskActive.php?task_id=${curTaskId}`);
            fetch(`https://cab07.000webhostapp.com/setTaskActive.php?task_id=${curTaskId}`);
            dispatch({
              type: "GoToSubPage",
              curSubPage: "MainMenu",
            });
       };   
    dispatch({
      type: "SetAnswer",
      curAns: "",
    });
  };
  return (
    <div className="TestPage">
      {curTests[curIndex]}
      <Button
        variant="contained"
        color="primary"
        className="TP_but"
        onClick={clickHandler}
      >
        Адказаць
      </Button>
    </div>
  );
};
