import React, { useEffect, useState } from "react";
import { withStyles } from "@material-ui/core/styles";
import MuiAccordion from "@material-ui/core/Accordion";
import MuiAccordionSummary from "@material-ui/core/AccordionSummary";
import MuiAccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";

const Accordion = withStyles({
  root: {
    border: "1px solid rgba(0, 0, 0, .125)",
    boxShadow: "none",
    "&:not(:last-child)": {
      borderBottom: 0,
    },
    "&:before": {
      display: "none",
    },
    "&$expanded": {
      margin: "auto",
    },
  },
  expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
  root: {
    backgroundColor: "rgba(0, 0, 0, .03)",
    borderBottom: "1px solid rgba(0, 0, 0, .125)",
    marginBottom: -1,
    minHeight: 56,
    "&$expanded": {
      minHeight: 56,
    },
  },
  content: {
    "&$expanded": {
      margin: "12px 0",
    },
  },
  expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiAccordionDetails);

interface IATI_AllQuests {
  curTest: any[];
}

export const ATI_AllQuests = ({ curTest }: IATI_AllQuests) => {
  const [expanded, setExpanded] = useState(0);
  const [curSet, setCurSet] = useState<any[]>([]);
  const handleChange = (panel: any) => {
      alert(panel)      
    setExpanded(panel);
  };
  useEffect(() => {   
    let arr: any[] = [];    
    for ( let i:number = 0; i<curTest.length; i++ ) {         
      arr.push(
        <Accordion         
          square
          expanded = { true }
          onChange={ () => handleChange(i)}
        >
          <AccordionSummary aria-controls={`panel${i}d-content`} id={`panel${i}d-header`}>
            <Typography>{curTest[i]!.id}</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              {curTest[i]!.quest }
              <br></br>
              <br></br> 
              { curTest[i]?.questText }
              <br></br>
              <br></br> 
              { curTest[i].answers?"1."+curTest[i]?.answers[0]:''}   
              <br></br>
              { curTest[i].answers?"2."+curTest[i]?.answers[1]:''}   
              <br></br>
              { curTest[i].answers?"3."+curTest[i]?.answers[2]:''}    
              <br></br>
              { curTest[i].answers?"4."+curTest[i]?.answers[3]:''}   
              <br></br>
              { curTest[i].answers?"5."+curTest[i]?.answers[4]:''}   
              <br></br>          
              <br></br> 
              { curTest[i].right }
            </Typography>
          </AccordionDetails>
        </Accordion>        
      );      
    };   
    setCurSet(arr);
  }, [curTest]);
  return (
    <div className = "ATI_AQ">
        { curSet }
    </div>
  );
};
