import { Box, Checkbox, FormControlLabel, makeStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "redux";

const useStyles = makeStyles({
  root: {
    "&:hover": {
      backgroundColor: "transparent",
    },
  },
  icon: {
    borderRadius: 3,
    width: 10,
    height: 10,
    boxShadow:
      "inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)",
    backgroundColor: "#f5f8fa",
    backgroundImage:
      "linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))",
    "$root.Mui-focusVisible &": {
      outline: "2px auto rgba(19,124,189,.6)",
      outlineOffset: 2,
    },
    "input:hover ~ &": {
      backgroundColor: "#ebf1f5",
    },
    "input:disabled ~ &": {
      boxShadow: "none",
      background: "rgba(206,217,224,.5)",
    },
  },
  checkedIcon: {
    backgroundColor: "#137cbd",
    backgroundImage:
      "linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))",
    "&:before": {
      display: "block",
      width: 11,
      height: 11,
      backgroundImage:
        "url(\"data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3E%3Cpath" +
        " fill-rule='evenodd' clip-rule='evenodd' d='M12 5c-.28 0-.53.11-.71.29L7 9.59l-2.29-2.3a1.003 " +
        "1.003 0 00-1.42 1.42l3 3c.18.18.43.29.71.29s.53-.11.71-.29l5-5A1.003 1.003 0 0012 5z' fill='%23fff'/%3E%3C/svg%3E\")",
      content: '""',
    },
    "input:hover ~ &": {
      backgroundColor: "#106ba3",
    },    
  },
  ff :{ 
    fontSize: 10
  }
});

export const TestPageTemplateA_2: React.FC<ITPT_A_0> = ( {question, questText, answers, right} ) => {
  const dispatch:Dispatch<any> = useDispatch();
  
  const curAnswer = useSelector((state:SiteState ) => state.curAnswer )
  const classes = useStyles();
  const [ch0, setCH0] = useState(0);
  const [ch1, setCH1] = useState(0);
  const [ch2, setCH2] = useState(0);
  const [ch3, setCH3] = useState(0);
  const [ch4, setCH4] = useState(0);

  useEffect(() => {    
    if ( curAnswer === "" ) {
      setCH0(0);
      setCH1(0);
      setCH2(0);
      setCH3(0);
      setCH4(0);
    }    
  }, [curAnswer]);
  useEffect(()=>{   
    dispatch({
      type : "SetAnswer",
      curAns : `${ch0===0?"":1}${ch1===0?"":2}${ch2===0?"":3}${ch3===0?"":4}${ch4===0?"":5}`
    })
  },[ch0, ch1, ch2, ch3, ch4]);
  
  return (
    <div className="TestPageTemplateA_2">
      <Box fontSize={35} className = "TPT_A_2_q">
        { question }
      </Box>
      <Box fontSize={22} >
        { questText }
      </Box>
      <div className="TPT_A_2">
        <FormControlLabel          
          control={
            <Checkbox
              className={classes.root}
              disableRipple
              color="default"
              checked={ch0 === 0 ? false : true}
              checkedIcon={<span className={classes.checkedIcon} />}
              icon={<span className={classes.icon} />}
              inputProps={{ "aria-label": "decorative checkbox" }}
              onChange={() => {
                setCH0(ch0 === 0 ? 1 : 0);
              }}
            />
          }
          label={answers[0]}
        />
        <FormControlLabel          
          control={
            <Checkbox
              className={classes.root}
              disableRipple
              color="default"
              checked={ch1 === 0 ? false : true}
              checkedIcon={<span className={classes.checkedIcon} />}
              icon={<span className={classes.icon} />}
              inputProps={{ "aria-label": "decorative checkbox" }}
              onChange={() => {
                setCH1(ch1 === 0 ? 2 : 0);
              }}
            />
          }
          label={answers[1]}
        />
        <FormControlLabel          
          control={
            <Checkbox
              className={classes.root}
              disableRipple
              color="default"
              checked={ch2 === 0 ? false : true}
              checkedIcon={<span className={classes.checkedIcon} />}
              icon={<span className={classes.icon} />}
              inputProps={{ "aria-label": "decorative checkbox" }}
              onChange={() => {
                setCH2(ch2 === 0 ? 3 : 0);
              }}
            />
          }
          label={answers[2]}
        />
        <FormControlLabel          
          control={
            <Checkbox
              className={classes.root}
              disableRipple
              color="default"
              checked={ch3 === 0 ? false : true}
              checkedIcon={<span className={classes.checkedIcon} />}
              icon={<span className={classes.icon} />}
              inputProps={{ "aria-label": "decorative checkbox" }}
              onChange={() => {
                setCH3(ch3 === 0 ? 4 : 0);
              }}
            />
          }
          label={answers[3]}
        />
        <FormControlLabel          
          control={
            <Checkbox
              className={classes.root}
              disableRipple
              color="default"
              checked={ch4 === 0 ? false : true}
              checkedIcon={<span className={classes.checkedIcon} />}
              icon={<span className={classes.icon} />}
              inputProps={{ "aria-label": "decorative checkbox" }}
              onChange={() => {
                setCH4(ch4 === 0 ? 5 : 0);
              }}
            />
          }
          label={answers[4]}
        />
      </div>
    </div>
  );
};
