import { useEffect, useState } from "react";

interface IATI_Answers {
    curTest: string[],
    userData : any[]
} 

export const ATI_Answers = ({curTest, userData}:IATI_Answers) => {
    
    const [tabCellsA, setTabCellsA] = useState<any[]>([]);
    const [tabCellsB, setTabCellsB] = useState<any[]>([]);

    const [ rightAns, setRightAns ] = useState<any[]>([]);
    const [ userAns, setUserAns] = useState<any[]>([]);

    useEffect(() => {
      console.log(userData)
        let arrA: any[] = [];
        let arrB: any[] = [];

        let arrUA: any[] = [];
        let arrTA: any[] = [];

        curTest.map((el: any) => {
          arrTA.push(el.right);
          if (el.id[0] === "A") {
            arrA.push(
              <td>
                {el.id}
              </td>
            );
          } else {
            arrB.push(
              <td>
                {el.id}
              </td>
            );
          }
        });
        userData.map( (el:any) => {
          arrUA.push({
            id : el.id,
            right : el.right,
            theme: el.theme,
            ans: el.ans
          })
        });
        setTabCellsA(arrA);
        setTabCellsB(arrB);

        setRightAns(arrTA);
        setUserAns(arrUA);
      }, [curTest, userData]);
 
  return (
    <div>
      <table className="ATI_tabA">
        <thead>
          <tr>
            <td></td>
            {tabCellsA}
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>right</td>
            {curTest.map((el: any) => {
              if (el.id[0] === "A") {
                return <td>{el.right}</td>;
              }
            })}
          </tr>
          <tr>
            <td>users</td>            
            {userAns.map((el: any) => {               
                if (el.id[0] === "A") {                  
                return <td className = { el.ans === rightAns[el.id.substr(1)/1-1]?"ATI_ANS_tr_right":"ATI_ANS_td_wrong"}>{el.ans}</td>;
              }
            })}
          </tr>
        </tbody>
      </table>
      <table className="ATI_tabB">
        <thead>
          <tr>
            <td></td>
            {tabCellsB}
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>right</td>
            {curTest.map((el: any) => {
              if (el.id[0] === "B") {
                return <td>{el.right}</td>;
              }
            })}
          </tr>
          <tr>
            <td>users</td>            
            {userAns.map((el: any) => {
              if (el.id[0] === "B") {
                return <td className = { el.ans === rightAns[el.id.substr(1)/1-1+30]?"ATI_ANS_tr_right":"ATI_ANS_td_wrong"}>{el.ans}</td>;
              }
            })}
          </tr>
        </tbody>
      </table>
    </div>
  );
};
