import { initState } from "./state";

const rootReducer = (
    state: SiteState = initState,
    action : SiteAction
): SiteState => {
    switch ( action.type ) {
        case "LogIn" :
            return  {
                ...state,
                curPage : "MainPage",
                curUserId : action.curUserId || 1000,
                curUserFio : action.curUserFio || "",
                curUserSchool : action.curUserSchool || "",
                curUserClass : action.curUserClass || ""
            }; 
        case "LogOut" : 
            return {
                ...state,
                curPage : "LoginPage",
                curSubPage : "Tasks",
                curUserId : -1,
                curUserFio : ""
            }; 
        case "GoToSubPage" :               
                return {
                    ...state,
                    curPage : "MainPage",
                    curSubPage: action.curSubPage||"MainMenu"
                };
        case "ShowTest" :
                return {
                    ...state,
                    curTestSet : action.curTestSet || '',
                    curSubPage : "TestPage",
                    curTaskId: action.curTaskId
                }
        case "SetAnswer" :
            return {
                ...state, 
                curAnswer: action.curAns || ""
            }        
        case "ShowAdminTestInfo" :
            return {
                ...state,
                curSubPage : "AdminTestInfo",
                curTestResultID: action.curTestResultID || -1
            }        
        default :
            return state;
    }    
}

export default rootReducer;