import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import { FormControl, InputLabel, MenuItem, Select } from "@material-ui/core";


interface IUserData {
  fio?:string,
  answers: string[],
  results: number[]
}

function TabPanel(props: any) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

// TabPanel.propTypes = {
//   children: PropTypes.node,
//   index: PropTypes.any.isRequired,
//   value: PropTypes.any.isRequired,
// };

function a11yProps(index: any) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

export const AdminStatistics = () => {
  const classes = useStyles();
  const [ value, setValue] = useState(0);
  const [ user, setUser] = useState<string>("");
  const [ users, setUsers] = useState<any[]>([]);
  const [ usersData, setUsersData ] = useState<any>([]);
  const [ curUserData, setCurUserData ] = useState<IUserData>({fio:"", answers: [], results:[]});
  const [ showingStats, setShowingStats ] = useState<any>();

  const handleChange = (event: any, newValue: any) => {
    setValue(newValue);
  };

  const handleUserChange = (event: any) => {
    setUser(event.target.value as string);
    usersData.forEach ( (el:IUserData) => {
      if ( el.fio === event.target.value ) {
        setCurUserData( {
          answers : el.answers,
          results : el.results 
        });      
      }
    })
  };
  
  useEffect(() => {
    let usArr:any[] = [];
    let usData: any[] = [];
    fetch(`https://cab07.000webhostapp.com/sql_getTestsResults.php`)
      .then((res) => res.json())
      .then((result) => {
        result.results.map ( (el:any) => {    
          let ind:number = usArr.indexOf(el.user_fio);
          if ( ind != -1 ) {
            usData[ ind ].answers.push(
              el.ans
            )
            usData[ ind ].results.push (
              el.result/1
            ) 
          }            
          else {
            usData.push ({
              fio: el.user_fio,
              answers: [],
              results : []
            });
            usArr.push( el.user_fio)
          }          
        })       
        setUsers(usArr);
        setUsersData(usData);  
      });         
  }, []);
  useEffect(() =>{
    let min = Math.min.apply(null, curUserData.results);
    let max = Math.max.apply(null, curUserData.results);
    let mid = (min+max)/2;
    setShowingStats(
      <div>
        <p>{`Всего тесто выполнено: ${curUserData.answers.length || ''}`}</p>
        <p>{`Минимальный балл: ${min}`}</p>
        <p>{`Максимальный балл: ${max}`}</p>
        <p>{`Средний балл: ${mid}`}</p>
        <p>Разбивка по темам:</p>
        <table className = "AStat_tab">
          <thead>
            <tr>
              <td></td>
              <td>slovo</td>
              <td>punk</td>
              <td>sint</td>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>min</td>
              <td>10</td>
              <td>10</td>
              <td>10</td>
            </tr>
            <tr>
              <td>max</td>
              <td>10</td>
              <td>10</td>
              <td>10</td>
            </tr>
            <tr>
              <td>mid</td>
              <td>10</td>
              <td>10</td>
              <td>10</td>
            </tr>
          </tbody>
        </table>
      </div>      
    )
  },[curUserData])
  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="simple tabs example"
        >
          <Tab label="Ученик" {...a11yProps(0)} />
          <Tab label="Класс" {...a11yProps(1)} />
          {/* <Tab label="Item Three" {...a11yProps(2)} /> */}
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        <FormControl className={classes.formControl}>
          <InputLabel id="demo-simple-select-label">User</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={user}
            onChange={handleUserChange}
          >
            {users.map( (el:any) => {
              return (<MenuItem value = {el}>{el}</MenuItem>)
            })}   
          </Select>
        </FormControl>
        { showingStats }
      </TabPanel>
      <TabPanel value={value} index={1}>
        Item Two
      </TabPanel>
    </div>
  );
};
