import {
  Box, 
  TextField,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "redux";

export const TestPageTemplateB_0: React.FC<ITPT_B_0> = ({
  question,
  questionText,
}) => {
  const [curAns, setCurAnswer] = useState("");
  const curStateAnswer = useSelector((state:SiteState) => state.curAnswer);
  const dispatch:Dispatch<any> = useDispatch();
  useEffect(() => {
    if ( curStateAnswer === "") {
      setCurAnswer("");
    }    
  },[curStateAnswer])
  useEffect (() => {
    dispatch({
      type : "SetAnswer",
      curAns : curAns.toUpperCase()
    })
  },[curAns]);
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
     setCurAnswer(e.target.value)
  }
  return (
    <div className="TestPageTemplateB_0">
      <Box fontSize = {35} className="TPT_A_2_q">{question}</Box>
      <Box fontSize={30}>{questionText}</Box>
      <TextField 
        id="outlined-basic" 
        label="" variant="outlined" 
        value = { curAns}
        onChange = {handleChange}/>
    </div>
  );
};
