import * as React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "redux";
// UsersPages
import { NavBar } from "./NavBar/Navbar";
import { Cabinet } from "./SubPages/Cabinet";
import { Tasks } from "./SubPages/Tasks";
import { TestsResults } from "./SubPages/TestsResults";
import { TestPage } from "./TestPage/TestPage";
// AdminPages
import { AdminAddTask } from './SubPages/Admin/AdminAddTask';
import { AdminResults } from './SubPages/Admin/AdminResults';
import { AdminStatistics } from './SubPages/Admin/AdminStatistics';
import { AdminTestInfo } from "./SubPages/Admin/AdminTestInfo";

export const MainPage: React.FC = () => {
  const curSubPage = useSelector((state: SiteState) => state.curSubPage);
  const [curShowSubPage, changeCurShowSubPage] = React.useState(<Tasks />);

  React.useEffect(() => {
    switch (curSubPage) {
      case "Tasks":
        changeCurShowSubPage(<Tasks />);
        break;
      case "TestsResults":
        changeCurShowSubPage(<TestsResults />);
        break;
      case "Cabinet":
        changeCurShowSubPage(<Cabinet />);
        break;     
      case "TestPage":
        changeCurShowSubPage(<TestPage />);
        break;
      case "AdminAddTask":
        changeCurShowSubPage(<AdminAddTask />);
        break;
      case "AdminResults" : 
        changeCurShowSubPage(<AdminResults />);
        break;
      case "AdminStatistics" :
        changeCurShowSubPage (<AdminStatistics />);
        break;
      case "AdminTestInfo" :
        changeCurShowSubPage (<AdminTestInfo />);
        // props ?????/reak;
        break;
        
      default: 
        changeCurShowSubPage(<Tasks />);
    }
  }, [curSubPage]);
  return (
    <div className = "MainPage">
      <NavBar />
      <div className="container">{curShowSubPage}</div>
    </div>
  );
};
