import {
  AppBar,
  Button,
  IconButton,
  makeStyles,
  Toolbar,
  Typography,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "redux";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

export const NavBar: React.FC = () => {
  const classes = useStyles();
  const curUserFio = useSelector((state: SiteState) => state.curUserFio);
  const curUserId = useSelector((state: SiteState) => state.curUserId);
  const [curNavMenu, setCurNavMenu] = useState(<></>);
  const dispatch: Dispatch<any> = useDispatch();

  const navHandler = (targetPage: string) => {
    dispatch({
      type: "GoToSubPage",
      curSubPage: targetPage,
    });
  };

  const exitHandler = () => {
    dispatch({
      type: "LogOut",
    });
  };
  useEffect(() => {
    setCurNavMenu(
      <>
        <Button color="inherit" onClick={() => navHandler("Tasks")}>
          Tasks
        </Button>
        <Button color="inherit" onClick={() => navHandler("TestsResults")}>
          Results
        </Button>
        <Button color="inherit" onClick={exitHandler}>
          Exit
        </Button>
      </>
    );
  }, []);
  useEffect(() => {
    if (curUserId < 0) {
      setCurNavMenu(
        <>
          <Button color="inherit" onClick={() => navHandler("AdminAddTask")}>
            {" "}
            Add Tasks
          </Button>
          <Button color="inherit" onClick={() => navHandler("AdminResults")}>
            Show Results
          </Button>
          <Button color="inherit" onClick={() => navHandler("AdminStatistics")}>
            Staticstics
          </Button>
          <Button color="inherit" onClick={exitHandler}>
            Exit
          </Button>
        </>
      );
    }
  }, [curUserId]);
  return (
    <AppBar position="static" className="NavBar">
      <Toolbar className="NavBar_1">
        <Button color="inherit"> {`Welcome, ${curUserFio}`}</Button>       
        <div className="NavBar_Nav">{curNavMenu}</div>
      </Toolbar>
    </AppBar>
  );
};
